.on-sale h2 {
  margin: 0 auto;
  font-family: 'Exo', sans-serif;
  font-weight: 500;
  font-size: 24px;
  text-transform: uppercase;
  color: #FFFFFF;
}

.tableRoot {
  color: #FFFFFF;
  border-spacing: 0 14px;
}

.tableRoot tr:nth-child(odd) {
  background-color: rgba(255, 255, 255, .05);
}
.tableRoot tr:nth-child(even) {
  background-color: rgba(255, 255, 255, .1);
}

.tableRoot th {
  padding: 9px 10px;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  text-align: center;
  text-transform: capitalize;
}
.tableRoot th:first-child:before,
.tableRoot th:last-child:before,
.tableRoot th:first-child:after,
.tableRoot th:last-child:after,
.tableRoot td:first-child:before,
.tableRoot td:last-child:before,
.tableRoot td:first-child:after,
.tableRoot td:last-child:after {
  content: '';
  position: absolute;
  background: #FFFFFF;
  width: 2px;
  height: 2px;
}
.tableRoot th:first-child:before,
.tableRoot td:first-child:before {
  left: 0;
  top: 0;
}
.tableRoot th:last-child:before,
.tableRoot td:last-child:before {
  right: 0;
  top: 0;
}
.tableRoot th:first-child:after,
.tableRoot td:first-child:after {
  bottom: 0;
  left: 0;
}
.tableRoot th:last-child:after,
.tableRoot td:last-child:after {
  right: 0;
  bottom: 0;
}

.tableRoot td {
  padding-top: 14px;
  padding-bottom: 14px;
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  text-align: center;
  color: #C4C4C4;
}

.tableRoot td:last-child {
  padding-right: 14px;
  padding-left: 10px;
}

.tableRoot th:first-child {
  width: 40px;
}

.star {
  padding-left: 15px;
  padding-right: 14px;
}



/*ARTICLE*/
.banner {
  flex: 0 0 10%;
  margin-left: 60px;
  min-width: 207px;
}
.banner h2 {
  margin: 0 auto 14px;
  font-family: 'Exo', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  text-transform: uppercase;
  color: #FFFFFF;
}

/*FOOTER*/
.footer {
  padding: 0 640px 25px;
  display: flex;
  background: #0B0C23
}

.border-container-icon {
  margin-right: 12px;
  padding: 10px 12px 8px 13px;
  background: rgba(255, 255, 255, .1);
}

